"use client";

import React from "react";
import Navbar from "@/components/navbar";
import Footer from "@/components/footer";
import Link from "next/link";
import {
  Card,
  CardContent,
  // CardDescription, // Not used
  // CardHeader, // Not used
  // CardTitle, // Not used
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
// Change: Switched from Drawer to Dialog for a compact popup look
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogClose,
} from "@/components/ui/dialog";
import {
  Home,
  Sparkles,
  Truck,
  Building2,
  BedDouble,
  Hammer,
  ArrowRight,
  CheckCircle,
  Check,
  X,
} from "lucide-react";

export default function Services() {
  const serviceDetails = [
    {
      id: "residential",
      title: "Residential Cleaning",
      description:
        "Consistent cleanliness for busy families and professionals.",
      longDescription:
        "For busy families, professionals, and anyone who wants a consistently clean home without the hassle. We create a healthy environment for you to relax in.",
      icon: Home,
      image:
        "https://images.unsplash.com/photo-1581578731117-104f2a863ecc?q=80&w=1000&auto=format&fit=crop",
      url: "/contact",
      includes: [
        "Dusting and wiping surfaces",
        "Vacuuming and mopping floors",
        "Bathrooms cleaned and sanitized",
        "Kitchen surfaces & appliances",
        "Emptying trash & bin liners",
        "Making beds (linens changed)",
      ],
    },
    {
      id: "deep-cleaning",
      title: "Deep Cleaning",
      description:
        "Detailed attention for spring cleaning or special occasions.",
      longDescription:
        "Perfect for spring cleaning, before guests arrive, or when your space needs extra attention. We go beyond the surface to remove hidden dust and grime.",
      icon: Sparkles,
      image:
        "https://images.unsplash.com/photo-1527512830971-1e9c269840b4?q=80&w=1000&auto=format&fit=crop",
      url: "/contact",
      includes: [
        "Everything in a standard clean",
        "Baseboards, frames, & switches",
        "Inside microwave & oven scrub",
        "Scrubbing tile grout in bathrooms",
        "Ceiling fans and light fixtures",
        "High-touch & hard-to-reach areas",
      ],
    },
    {
      id: "move-in-out",
      title: "Move-In / Move-Out",
      description: "Ensure the property is pristine for the next chapter.",
      longDescription:
        "Ideal for landlords, tenants, and homeowners. Whether you want your deposit back or a fresh start in a new home, we leave the place spotless.",
      icon: Truck,
      image:
        "https://images.unsplash.com/photo-1560448204-e02f11c3d0e2?q=80&w=1000&auto=format&fit=crop",
      url: "/contact",
      includes: [
        "Inside cabinets and drawers",
        "Inside fridge, oven, dishwasher",
        "Deep scrub of bathrooms",
        "Floors vacuumed & mopped",
        "Spot cleaning walls/baseboards",
        "Window sills and tracks",
      ],
    },
    {
      id: "commercial",
      title: "Commercial & Office",
      description: "Professional spaces kept clean for employees and clients.",
      longDescription:
        "Keep your workplace clean, professional, and welcoming. A clean office boosts productivity and leaves a great first impression on clients.",
      icon: Building2,
      image:
        "https://images.unsplash.com/photo-1497366216548-37526070297c?q=80&w=1000&auto=format&fit=crop",
      url: "/contact",
      includes: [
        "Daily, weekly, or bi-weekly",
        "Sanitizing restrooms & break rooms",
        "Desks, phones, touch points",
        "Trash removal & recycling",
        "Floor care (vacuuming/mopping)",
        "Supply restocking (soap, paper)",
      ],
    },
    {
      id: "airbnb",
      title: "Airbnb & Turnover",
      description: "Fast, reliable turnovers to maintain Superhost status.",
      longDescription:
        "Quick, reliable turnovers between guests. We understand the tight schedules of short-term rentals and ensure 5-star cleanliness every time.",
      icon: BedDouble,
      image:
        "https://images.unsplash.com/photo-1613425669932-d7904a08f58c?q=80&w=1000&auto=format&fit=crop",
      url: "/contact",
      includes: [
        "Guest-ready presentation",
        "Fresh bedding & towel setup",
        "Reporting damages or low supplies",
        "Deep sanitization of touch areas",
        "Laundry service (wash & fold)",
        "Same-day turnover available",
      ],
    },
    {
      id: "renovation",
      title: "Post-Renovation",
      description: "Removing dust and debris so you can enjoy your new space.",
      longDescription:
        "Construction is messy. We handle the fine dust and debris left behind by contractors so you can move your furniture in immediately.",
      icon: Hammer,
      image:
        "https://images.unsplash.com/photo-1505798577917-a651a5d6a321?q=80&w=1000&auto=format&fit=crop",
      url: "/contact",
      includes: [
        "High-dust removal (walls/ceilings)",
        "Surface wipe-down & polishing",
        "Paint spot removal from floors",
        "Cleaning inside empty cabinets",
        "Window sticker & residue removal",
        "Final detail inspection",
      ],
    },
  ];

  const addOns = [
    "Inside Fridge",
    "Inside Oven",
    "Inside Cabinets",
    "Laundry Wash & Fold",
    "Balcony Cleaning",
    "Carpet Shampooing",
    "Pet Hair Removal",
    "Wall Washing",
    "Window Cleaning (Interior)",
  ];

  return (
    <main className="min-h-screen bg-background selection:bg-primary/20">
      <Navbar />

      {/* Hero Section */}
      <section className="pt-32 pb-16 bg-gradient-to-b from-muted/50 to-background">
        <div className="container mx-auto px-4 md:px-6">
          <div className="text-center max-w-3xl mx-auto space-y-4">
            <div className="inline-flex items-center gap-2 bg-primary/10 rounded-full px-4 py-1.5 animate-in fade-in slide-in-from-bottom-4 duration-500">
              <Sparkles className="h-4 w-4 text-primary" />
              <span className="text-primary text-sm font-semibold tracking-wide uppercase">
                Expert Cleaning Services
              </span>
            </div>
            <h1 className="text-4xl md:text-5xl lg:text-6xl font-extrabold tracking-tight text-foreground animate-in fade-in slide-in-from-bottom-5 duration-700 delay-100">
              Spotless Cleaning for <br className="hidden sm:block" />
              <span className="text-primary">Every Space</span>
            </h1>
            <p className="text-lg md:text-xl text-muted-foreground animate-in fade-in slide-in-from-bottom-6 duration-700 delay-200">
              Professional, reliable, and thorough. Choose the perfect service package aimed at restoring freshness to your home or office.
            </p>
          </div>
        </div>
      </section>

      {/* Services Grid */}
      <section className="py-12 md:py-20 px-4 md:px-6 container mx-auto">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 lg:gap-8">
          {serviceDetails.map((service) => {
            const Icon = service.icon;
            return (
              <Card
                key={service.id}
                className="group relative flex flex-col overflow-hidden border-border/60 hover:border-primary/50 hover:shadow-2xl transition-all duration-300 hover:-translate-y-1 bg-card"
              >
                {/* Image Area */}
                <div className="relative h-56 overflow-hidden">
                  <img
                    src={service.image}
                    alt={service.title}
                    className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent opacity-80 group-hover:opacity-70 transition-opacity" />
                  
                  {/* Floating Icon */}
                  <div className="absolute bottom-4 left-4 flex items-center gap-3">
                    <div className="h-12 w-12 bg-primary text-primary-foreground rounded-xl flex items-center justify-center shadow-lg transform group-hover:rotate-6 transition-transform duration-300">
                      <Icon className="h-6 w-6" />
                    </div>
                    <h3 className="text-xl font-bold text-white leading-tight">
                      {service.title}
                    </h3>
                  </div>
                </div>

                <CardContent className="flex-1 flex flex-col p-6">
                  <p className="text-muted-foreground mb-6 line-clamp-2">
                    {service.description}
                  </p>

                  <div className="mt-auto">
                    {/* CHANGED: Using Dialog for a more compact, centered popup */}
                    <Dialog>
                      <DialogTrigger asChild>
                        <Button
                          className="w-full group-hover:bg-primary/90 transition-all text-base font-medium"
                          variant="outline"
                        >
                          View Details
                          <ArrowRight className="ml-2 h-4 w-4 transition-transform group-hover:translate-x-1" />
                        </Button>
                      </DialogTrigger>
                      
                      {/* Dialog Content: Compact, Max Width Limited, Rounded */}
                      <DialogContent className="sm:max-w-[850px] p-0 overflow-hidden gap-0 border-none shadow-2xl bg-card">
                        <div className="flex flex-col md:flex-row h-full md:max-h-[600px]">
                          
                          {/* Left Side: Image (Hidden on small mobile, visible on larger) */}
                          <div className="w-full md:w-2/5 h-48 md:h-auto relative bg-muted shrink-0">
                            <img 
                              src={service.image} 
                              alt={service.title}
                              className="absolute inset-0 w-full h-full object-cover"
                            />
                            <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent md:hidden" />
                            {/* Icon overlay for mobile only */}
                            <div className="absolute bottom-3 left-4 md:hidden flex items-center gap-2 text-white">
                                <Icon className="h-5 w-5" />
                                <span className="font-bold text-lg">{service.title}</span>
                            </div>
                          </div>

                          {/* Right Side: Content Area */}
                          <div className="flex-1 flex flex-col w-full md:w-3/5">
                            
                            {/* Header */}
                            <div className="p-6 pb-2 relative">
                              {/* Close button handles itself via DialogPrimitive, or we can add absolute close */}
                              <div className="flex items-center gap-3 mb-2">
                                <div className="hidden md:flex h-9 w-9 bg-primary/10 rounded-lg items-center justify-center text-primary shrink-0">
                                  <Icon className="h-5 w-5" />
                                </div>
                                <div>
                                    <DialogTitle className="text-2xl font-bold text-foreground">
                                        {service.title}
                                    </DialogTitle>
                                    <DialogDescription className="text-xs text-muted-foreground mt-0.5 uppercase tracking-wider font-medium">
                                        Professional Service Package
                                    </DialogDescription>
                                </div>
                              </div>
                            </div>

                            {/* Scrollable Body */}
                            <div className="px-6 py-2 overflow-y-auto flex-1 custom-scrollbar">
                                <div className="space-y-6">
                                    <div className="bg-muted/30 p-3 rounded-lg border border-border/50 text-sm text-muted-foreground leading-relaxed">
                                        {service.longDescription}
                                    </div>

                                    <div>
                                        <h4 className="text-sm font-semibold text-foreground mb-3 flex items-center gap-2">
                                            <CheckCircle className="h-4 w-4 text-primary" />
                                            Package Inclusions
                                        </h4>
                                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-x-4 gap-y-2">
                                            {service.includes.map((item, idx) => (
                                                <div key={idx} className="flex items-start gap-2 text-sm">
                                                    <Check className="h-3.5 w-3.5 text-primary mt-1 shrink-0" />
                                                    <span className="text-muted-foreground">{item}</span>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {/* Footer / Actions */}
                            <div className="p-6 pt-4 border-t border-border/40 bg-muted/10 mt-auto">
                                <div className="flex gap-3">
                                    <DialogClose asChild>
                                        <Button variant="outline" className="flex-1">Close</Button>
                                    </DialogClose>
                                    <Link href={service.url} className="flex-1">
                                        <Button className="w-full">
                                            Book Now
                                        </Button>
                                    </Link>
                                </div>
                            </div>
                          </div>
                        </div>
                      </DialogContent>
                    </Dialog>
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>
      </section>

      {/* Add-ons Section */}
      <section className="py-16 bg-muted/30 border-t border-border">
        <div className="container mx-auto px-4 md:px-6">
          <div className="text-center mb-10 md:mb-14">
            <h2 className="text-3xl md:text-4xl font-bold text-foreground mb-4">
              Customize Your Clean
            </h2>
            <p className="text-muted-foreground max-w-2xl mx-auto text-lg">
              Need a little extra? Add these specialized services to any package to tailor the cleaning to your specific needs.
            </p>
          </div>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 max-w-6xl mx-auto">
            {addOns.map((addon, index) => (
              <div
                key={index}
                className="flex items-center gap-3 p-4 rounded-xl border border-border bg-background shadow-sm hover:shadow-md hover:border-primary/40 hover:bg-primary/5 transition-all duration-200 cursor-default group"
              >
                <div className="h-8 w-8 rounded-full bg-primary/10 flex items-center justify-center flex-shrink-0 group-hover:scale-110 transition-transform">
                  <Check size={16} className="text-primary" />
                </div>
                <span className="text-foreground font-medium text-sm md:text-base">{addon}</span>
              </div>
            ))}
          </div>
        </div>
      </section>

      <Footer />
    </main>
  );
}